import React from 'react';
import Card from '../Card/Card';
import s from "./Works.module.css";
import img1 from "../../assets/img/1.png";
import img2 from "../../assets/img/2.png";
import img3 from "../../assets/img/3.png";
const Works = (props) => {
    return (
        <div className={s.container}>
            <div className={s.content}>
                <Card
                    title='Баннеры'
                    decription={["Баннеры для постов, промо-блоков и т.д.", "Используемый метод разработки — коллажирование."]}
                    skills={["Ps"]}
                    img={img1}
                />
                <Card
                    title='Lost Shirt'
                    decription={["Вёрстка и frontend сайта принтов для одежды."]}
                    skills={["Html", "Css", "Js", "GitHub"]}
                    img={img2}
                />
                <Card
                    title='Анион'
                    decription={["Редизайн сайта компании «Анион»",
                        "Были разработаны UI-kit: система компонентов и стилей."]}
                    skills={["Figma", "Ps",]}
                    img={img3}
                />
            </div>
        </div>
    );
};

export default Works;